<template>

    <el-dialog title="办事指类别" v-model="is_show_catelist" @close="close">
        <div class="cs-box cs-bszn-cate-handle">
            <label @click="showEditBox"><img src="../../img/icon-add-cate.png"/></label>
            <div class="cs-box cs-bszn-edit-box" v-show="isShowEditBox"><input type="text" maxlength='7' class="" v-model="catename"><el-button @click="saveData">保存</el-button><el-button @click="cancleEditBox">取消</el-button></div>
        </div>
        <div class="cs-bszn-cate-list-tab" :style="dialogMainStyle">
            <el-table :data="subject_items">
                <el-table-column property="name" label="名称" ></el-table-column>
                <el-table-column label="操作">
                    <template scope="scope">
                        <el-button @click="update(scope.$index, scope.row)" type="text" size="small">编辑</el-button>
                        <el-button @click="deleteCate(scope.$index, scope.row)"  type="text" size="small">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
    </el-dialog>

</template>

<script>

export default {
    props: {
        is_show_catelist :{
            type:Boolean,
            default:false
        },
        mapHeight:{
            type:Number,
            default:600
        },
        subject_items :{
            type:Array,
            default:[]
        }
    },

    close: function() {
        console.log("myclose");
    },
    data: function () {
        return {
            dialogMainHeight:500,
            mapStyle:{
                width:'40%',
                height:this.mapHeight +'px'
            },
            catename:"",
            cateid:null,
            isShowEditBox:false

        }
    },
    methods: {
        showEditBox() {
            this.cateid = null;
            this.catename = "";
            this.isShowEditBox = true;
        },
        cancleEditBox() {
            this.isShowEditBox = false;
        },
        update(index, row) {
            this.isShowEditBox = true;
            this.cateid = row.id;
            this.catename = row.name;

        },
        close() {
            this.$emit('hideCatedialog');

        },
        doUpdateDate(token) {
            let that = this;

            this.$http.post(global.config.weburl + "api/sysSubject/updateSysSubject.do",
                    {token:token, name:that.catename, id:that.cateid})
                    .then(
                            function(res){
                                if(res.data.code==0){
                                    that.cateid = null;
                                    that.catename = "";
                                    that.isShowEditBox = false;
                                    that.$emit('changeCateList');

                                }else{
                                    global.tools.handleError(res.data);
                                }
                            });
        },
        doSaveDate(token) {
            let that = this;

            this.$http.post(global.config.weburl + "api/sysSubject/addSysSubject.do",
                {token:token, name:that.catename, isUseable:1,isComment:1})
                .then(
                    function(res){
                        if(res.data.code==0){
                            that.cateid = null;
                            that.catename = "";
                            that.isShowEditBox = false;
                            that.$emit('changeCateList');

                        }else{
                            global.tools.handleError(res.data);
                        }
            });
        },
        saveData() {
            let that = this;
            let token = global.data.getToken();
            if(!token) {
                return false;
            }
            if (!this.catename) {
                this.$message({
                    type: 'error',
                    message: "请输入类别名称不超过30个字"
                });
                return false
            }
            if (!this.catename) {
                this.$message({
                    type: 'error',
                    message: "请输入类别名称"
                });
                return false;
            }
            if (this.cateid) {
                that.doUpdateDate(token);
            } else {
                that.doSaveDate(token);
            }
        },
        deleteCate(index, row) {
            //console.log("=================");
            let that = this;
            let token = global.data.getToken();
            if(!token) {
                return false;
            }
            this.$http.post(global.config.weburl + "api/sysSubject/deleteSysSubject.do",
                {token:token, id:row.id})
                .then(
                        function(res){
                            if(res.data.code==0){
                                that.cateid = null;
                                that.catename = "";
                                that.isShowEditBox = false;
                                this.$message({
                                    type: 'success',
                                    message: "删除办事指南类别成功"
                                });
                                that.$emit('changeCateList');
                            }else{
                                global.tools.handleError(res.data);
                            }
                        }
                );

        }


    },
    created: function () {
        this.dialogMainHeight = this.mapHeight - 100;
        this.dialogMainStyle={
            "max-height":this.dialogMainHeight +'px'
        };
    },
    mounted:function() {

    }

}
</script>
<style>
.cs-bszn-cate-handle{padding-bottom: 10px;}
.cs-bszn-edit-box{margin-left: 40px;}

.cs-bszn-edit-box input {margin-right: 20px;width: 200px;}
.cs-bszn-cate-list-tab{
    overflow-y:auto;
}
</style>


